@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-300.woff2") format("woff2"), url("../fonts/roboto-condensed-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-400.woff2") format("woff2"), url("../fonts/roboto-condensed-400.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-700.woff2") format("woff2"), url("../fonts/roboto-condensed-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-600.woff2") format("woff2"), url("../fonts/alumni-sans-600.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-700.woff2") format("woff2"), url("../fonts/alumni-sans-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Alumni Sans";
  src: url("../fonts/alumni-sans-800.woff2") format("woff2"), url("../fonts/alumni-sans-800.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.clear-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.callback {
  color: #ffffff;
  background-color: #102654;
}
.callback h2 {
  font-family: "Alumni Sans", "Verdana", sans-serif;
  font-weight: 800;
  font-size: 54px;
  line-height: 156%;
  letter-spacing: 0.03em;
}

.catalog {
  position: relative;
  background-color: #f8f9fc;
  padding: 120px 0;
  z-index: 0;
}
@media (max-width: 1199px) {
  .catalog {
    padding: 42px 0 100px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 48px 0 70px;
  }
}
.catalog h2 {
  margin: 0 0 17px;
  font-family: "Alumni Sans", "Verdana", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  text-align: center;
}
@media (max-width: 1199px) {
  .catalog h2 {
    margin: 0 0 11px;
    font-size: 42px;
  }
}
@media (max-width: 767px) {
  .catalog h2 {
    font-size: 32px;
  }
}

@media (max-width: 1199px) {
  .catalog__container {
    padding: 0 118px;
  }
}
@media (max-width: 767px) {
  .catalog__container {
    padding: 0;
  }
}

.catalog__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  width: 100%;
}
@media (max-width: 1199px) {
  .catalog__list {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 767px) {
  .catalog__list {
    gap: 35px;
  }
}

.catalog__item {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 448px;
  padding: 63px 45px;
  text-align: center;
  background-color: #ffffff;
  z-index: 2;
}
@media (max-width: 767px) {
  .catalog__item {
    padding: 51px 35px;
    min-height: 384px;
  }
}
.catalog__item:hover::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border: 7px solid #00359f;
  border-image: linear-gradient(to left top, #00359f, #d10a42);
  border-image-slice: 1;
  pointer-events: none;
}
.catalog__item h3 {
  margin: 0 0 3px;
  font-weight: 700;
  font-size: 26px;
  line-height: 40px;
}
@media (max-width: 767px) {
  .catalog__item h3 {
    margin: 0 0 9px;
    font-size: 22px;
    line-height: 24px;
  }
}
.catalog__item p {
  margin: 0 0 52px;
  color: #495a7d;
  font-weight: 400;
  line-height: 24px;
}
@media (max-width: 767px) {
  .catalog__item p {
    margin: 0 0 48px;
  }
}
.catalog__item span {
  position: relative;
  margin: 0 0 34px;
  color: #103aae;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
}
@media (max-width: 767px) {
  .catalog__item span {
    margin: 5px 0 26px;
    font-size: 48px;
    line-height: 72px;
  }
  .catalog__item span svg {
    width: 24px;
    height: 37px;
  }
}
.catalog__item span::after {
  position: absolute;
  top: -60%;
  left: 14%;
  content: attr(data-shadow);
  font-size: 100px;
  line-height: 150px;
  color: rgba(232, 232, 232, 0.48);
  z-index: -1;
}
@media (max-width: 767px) {
  .catalog__item span::after {
    font-size: 80px;
    line-height: 120px;
  }
}

.catalog__image {
  position: absolute;
  right: 0;
  bottom: -150px;
  z-index: 1;
}
@media (max-width: 1199px) {
  .catalog__image {
    bottom: -131px;
  }
}
.catalog__image img {
  width: 100%;
  height: 100%;
}
@media (max-width: 767px) {
  .catalog__image {
    display: none;
  }
}

.catalog__tabs {
  position: relative;
  display: flex;
  justify-content: center;
  gap: 46px;
  max-width: 343px;
  margin: 0 auto 52px;
}
@media (max-width: 1199px) {
  .catalog__tabs {
    max-width: 315px;
    margin: 0 auto 58px;
  }
}
@media (max-width: 767px) {
  .catalog__tabs {
    margin: 0 auto 48px;
  }
}
.catalog__tabs::after {
  position: absolute;
  left: 0;
  bottom: -15px;
  content: "";
  height: 2px;
  width: 100%;
  background-color: rgba(120, 120, 120, 0.2);
}

.catalog__tab {
  font-size: 20px;
  line-height: 30px;
  color: rgba(16, 38, 84, 0.5);
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .catalog__tab {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 767px) {
  .catalog__tab {
    font-size: 16px;
    line-height: 24px;
  }
}
.catalog__tab:focus, .catalog__tab:hover {
  outline: none;
  color: rgba(16, 38, 84, 0.7);
}
.catalog__tab:active {
  color: #d10a42;
}
.catalog__tab--active {
  color: #102654;
  position: relative;
}
.catalog__tab--active:focus, .catalog__tab--active:hover {
  color: #102654;
}
.catalog__tab--active::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  bottom: -15px;
  width: 100%;
  height: 2px;
  background-color: #d10a42;
}
@media (max-width: 1199px) {
  .catalog__tab--active::after {
    bottom: -14px;
  }
}
@media (max-width: 767px) {
  .catalog__tab--active::after {
    width: calc(100% + 7px);
  }
}

.catalog__btn {
  color: #102654;
  max-width: 206px;
  padding: 28px 39px;
  margin: 0 auto;
  border: 2px solid #102654;
}
.catalog__btn:hover, .catalog__btn:focus {
  outline: none;
  border: none;
  color: #ffffff;
  background-image: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
}

.contacts {
  color: #102654;
}
.contacts h2 {
  font-family: "Alumni Sans", "Verdana", sans-serif;
  font-weight: 600;
  font-size: 54px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-bottom: 38px;
}
@media (max-width: 1199px) {
  .contacts h2 {
    font-size: 42px;
    margin-bottom: 32px;
  }
}
@media (max-width: 767px) {
  .contacts h2 {
    font-size: 32px;
    margin-bottom: 36px;
  }
}

.contacts__item h3 {
  font-weight: 700;
  margin: 0;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    font-size: 20px;
    margin-bottom: 6px;
  }
}
.contacts__item address,
.contacts__item p,
.contacts__item a {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #102654;
  margin: 0;
}
@media (max-width: 1199px) {
  .contacts__item address,
.contacts__item p,
.contacts__item a {
    font-size: 18px;
    line-height: 27px;
  }
}

.features {
  padding: 97px 0 96px;
  background-color: #f8f9fc;
}
@media (max-width: 1199px) {
  .features {
    padding: 92px 0;
  }
}
@media (max-width: 767px) {
  .features {
    padding: 59px 0 50px;
  }
}
.features h2 {
  margin: 0 0 12px;
  font-family: "Alumni Sans", "Verdana", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  text-align: center;
}
@media (max-width: 1199px) {
  .features h2 {
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .features h2 {
    margin: 0 0 13px;
    font-size: 32px;
  }
}

.features__container {
  width: 100%;
  max-width: 1366px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1199px) {
  .features__container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .features__container {
    padding: 0;
  }
}

.features__list {
  display: grid;
  grid-template-columns: 2fr 1fr 2fr;
  grid-auto-flow: dense;
  row-gap: 83px;
}
@media (max-width: 1199px) {
  .features__list {
    grid-template-columns: 1fr 1fr;
    row-gap: 0;
  }
}
@media (max-width: 767px) {
  .features__list {
    grid-template-columns: 1fr;
    row-gap: 0;
  }
}

.features__item:not(.features__item--big) {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 30px;
}
@media (max-width: 767px) {
  .features__item:not(.features__item--big) {
    grid-template-columns: 1fr;
    gap: 0;
    justify-items: center;
    text-align: center;
  }
}

.features__item:nth-child(2n) {
  padding: 32px 0 0 81px;
}
@media (max-width: 1199px) {
  .features__item:nth-child(2n) {
    padding: 22px 0 0 0;
  }
}
@media (max-width: 767px) {
  .features__item:nth-child(2n) {
    padding: 15px 36px 15px;
  }
}

.features__item:nth-child(2n+3) {
  padding: 32px 0 0 65px;
}
@media (max-width: 1199px) {
  .features__item:nth-child(2n+3) {
    padding: 23px 0 0 15px;
  }
}
@media (max-width: 767px) {
  .features__item:nth-child(2n+3) {
    padding: 15px 36px 15px;
  }
}

.features__item--big {
  grid-column: 2/3;
  grid-row: span 2;
  justify-self: center;
  max-width: 240px;
  padding: 37px 28px 59px;
  margin: 41px 0 43px;
  background-color: #1c3374;
  color: #ffffff;
  text-align: center;
}
@media (max-width: 1199px) {
  .features__item--big {
    grid-column: 1/-1;
    grid-row: 2/3;
    max-width: 442px;
    padding: 14px 113px 39px;
    margin: 40px 0 12px;
  }
}
@media (max-width: 767px) {
  .features__item--big {
    grid-row: 3/4;
    max-width: 100%;
    padding: 20px 70px 44px;
    margin: 30px 0 18px;
  }
}

.features__description h3 {
  margin: 0 0 11px;
  font-weight: 700;
  font-size: 22px;
  line-height: 33px;
}
@media (max-width: 1199px) {
  .features__description h3 {
    margin: 6px 0 0;
    font-size: 20px;
  }
}
@media (max-width: 767px) {
  .features__description h3 {
    line-height: 30px;
  }
}
.features__description p {
  margin: 0;
  line-height: 22px;
}

.features__description--big p {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.features__value:not(.features__value--big) {
  display: flex;
  flex-direction: column;
